<template>
	<div id="activity-index">
		<public-header title="店铺活动"></public-header>
		<div class="content">
			<div class="wrap-search">
				<div class="tab-group">
					<div class="tab-item active">新建活动</div>
					<router-link tag="div" class="tab-item" :to="util.getUrl({path: 'pages/activity/list'})">已创建活动</router-link>
					<router-link tag="div" class="tab-item" :to="util.getUrl({path: 'pages/activity/bargainList'})">天天特价</router-link>
				</div>
			</div>
			<van-list class="list">
				<div class="list-item" v-if="perm.discount && perm.discount.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">店铺满减</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.discount.status}})" v-if="activity.discount">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/deliveryFee', query: {type: 'discount'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>配置店铺满减活动, 吸引更多用户下单</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="perm.cashGrant && perm.cashGrant.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">返现优惠</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.cashGrant.status}})" v-if="activity.cashGrant">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/deliveryFee', query: {type: 'cashGrant'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>配置店铺返现优惠活动, 吸引更多用户下单</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="perm.newMember && perm.newMember.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">门店新用户</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.newMember.status}})" v-if="activity.newMember">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/deliveryFee', query: {type: 'newMember'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>新用户下单满足条件后可享受减免优惠</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="perm.grant && perm.grant.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">满赠活动</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.grant.status}})" v-if="activity.grant">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/deliveryFee', query: {type: 'grant'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>下单满足条件后可获得赠品</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="perm.selfDelivery && perm.selfDelivery.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">自提打折</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.selfDelivery.status}})" v-if="activity.selfDelivery">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/deliveryFee', query: {type: 'selfDelivery'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>自提满足条件后可享受打折优惠</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="perm.couponCollect && perm.couponCollect.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">进店领券</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.couponCollect.status}})" v-if="activity.couponCollect">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/couponCollect', query: {type: 'couponCollect'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>顾客进店可领取代金券</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="perm.couponGrant && perm.couponGrant.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">下单返券</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.couponGrant.status}})" v-if="activity.couponGrant">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/couponCollect', query: {type: 'couponGrant'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>下单满足条件后可返代金券</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="perm.bargain && perm.bargain.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">天天特价</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.bargain.status}})" v-if="0&&activity.bargain">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/bargain'})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>下单满足条件后可返代金券</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="perm.deliveryFeeDiscount && perm.deliveryFeeDiscount.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">满减配送费</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.deliveryFeeDiscount.status}})" v-if="activity.deliveryFeeDiscount">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/deliveryFee', query: {type: 'deliveryFeeDiscount'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>配送费满足条件后可享受减免优惠</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="perm.selfPickup && perm.selfPickup.status == 1">
					<div class="flex-lr margin-15-b">
						<div class="title">自提满减</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.selfPickup.status}})" v-if="activity.selfPickup">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/deliveryFee', query: {type: 'selfPickup'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>配置自提满减活动,吸引更多用户下单</div>
						<div v-if="0">308户已创建</div>
					</div>
				</div>
				<div class="list-item" v-if="util.checkplugin('svip')">
					<div class="flex-lr margin-15-b">
						<div class="title">超级会员红包</div>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/list', query: {status: activity.svipRedpacket.status}})" v-if="activity.svipRedpacket">已创建</router-link>
						<router-link tag="div" class="create" :to="util.getUrl({path: 'pages/activity/svipRedpacket', query: {type: 'svipRedpacket'}})" v-else>立即创建</router-link>
					</div>
					<div class="flex-lr font-12">
						<div>配置超级会员红包,吸引更多用户下单</div>
					</div>
				</div>
			</van-list>
		</div>
		<iloading v-if="showPreLoading"></iloading>
	</div>
</template>

<script>
	import publicHeader from "@/components/header"
	import {getData} from "@/controller/funcCommon"
	export default {
		components: {
			publicHeader,
		},
		data() {
			return {
				showPreLoading: true,
				activity: {},
				perm: {}
			}
		},
		methods: {
			onLoad() {
				var that = this;
				getData({
					vue: that,
					url: 'manage/activity/index',
					data: {
						_account_perm: 1,
					},
					success: function(res) {
						that.activity = res.activity;
						that.perm = res.perm;
					}
				})
			},
		},
		mounted() {
			this.onLoad();
		}
	}
</script>

<style>
	#activity-index .list-item{
		margin: 15px;
		padding: 15px;
		border-radius: 5px;
		background: url("../../../static/img/activity_bg_1.png") no-repeat;
		background-size: 100% 100%;
		color: #fff;
	}
	#activity-index .list-item .create{
		background-color: #fff;
		color: #FD6B38;
		font-size: 12px;
		padding: 5px 10px;
		border-radius: 20px;
	}
</style>


